export default{
	name:'Searchtravel',
	data(){
		return{
			history:[],
			searchdata:'',
			hasStorage:false,
			searchRes:[],
			nodata:false
		}
	},
	created() {
		this.getStorage()
	},
	methods:{
		tapHistory(item){
			console.log(item)
			this.searchdata = item
			this.nodata = false
			this.hasStorage = false
			this.setStorage(item)
			this.searchdb(item)
		},
		gosearch(){
			var searchKey = this.searchdata
			if(searchKey != ''){
				console.log('搜索')
				this.nodata = false
				this.hasStorage = false
				this.setStorage(searchKey)
				this.searchdb(searchKey)
			}
		},
		setStorage(val){
			const searchArr = uni.getStorageSync('search_key') || []
			searchArr.unshift(val)
			uni.setStorageSync('search_key',searchArr)
		},
		// 取出本地缓存
		getStorage(){
			var getdata = uni.getStorageSync('search_key')
			var getdataArr = Array.from(new Set(getdata))
			if(getdata == '' || getdata == []){
				this.hasStorage = false
			}else{
				this.history = getdataArr
				this.hasStorage = true
			}
		},
		// 清楚搜索历史
		delStorage(){
			uni.removeStorageSync('search_key')
			this.getStorage()
		},
		// 搜索数据库
		searchdb(searchKey){
			const db = wx.cloud.database()
			db.collection('usersUploadData').where({
				datainfo:{
					uploadContent:db.RegExp({
						regexp:searchKey,
						options:'m'
					})
				}
			}).get().then(res => {
				console.log(res)
				if(res.data.length == 0){
					this.nodata = true
					this.hasStorage = false
				}
				this.searchRes = res.data
			})
		},
		// 点击结果
		itemTap(id){
			console.log(id)
			uni.navigateTo({
				url:'../details/details?id='+id
			})
		}
	}
}
